import React from "react";
import BeerCard from "./BeerCard";
import ProgressIndicator from "./ProgressIndicator";
import Dialog from "material-ui/Dialog";
import FlatButton from "material-ui/FlatButton";

const BeerList = ({ isLoading, user, beers, updateInventory }) => {
  if (isLoading) return <ProgressIndicator />;
  if (!beers || !Object.keys(beers).length)
    return (
      <Dialog
        title="No beers ðŸš±"
        actions={
          <FlatButton onClick={() => (window.location = "/scan")} color="primary">
            Add a beer
          </FlatButton>
        }
        open={true}
        modal={true}
      >
        No beers are in the fridge right now, check back later.
      </Dialog>
    );
  return Object.keys(beers).map((key) => (
    <BeerCard id={key} key={key} user={user} onConsumed={() => updateInventory(key, -1)} {...beers[key]} />
  ));
};

export default BeerList;
